.container {
  display: flex;
  flex-direction: column;
  max-width: 296px;
  margin-right: 32px;
  justify-content: space-between;
}

.week {
  padding: 25px;
  padding-bottom: 135px;
  background-color: var(--greyF4);
  margin-bottom: 32px;
}

.weekday {
  margin-bottom: 15px;
  font-family: 'SF-Bold';
  font-size: 24px;
}

.pomodors {
  display: flex;
  flex-direction: column;
  background-color: var(--greyF4);
  font-family: 'SF-Bold';
  font-size: 24px;
}

.pomodors > span {
  display: flex;
  align-items: center;
  padding: 25px;
  align-self: center;
  justify-self: center;
  color: var(--grey99);
}
.pomodors>span>svg {
  margin-right: 18px;
}

.pomodors > div {
  display: flex;
  justify-content: center;
  background-color: var(--red);
}

.pomodors>div>p {
  margin: 0;
  margin: 9px 0;
  color: var(--white);
}
